.nosotros-container{
    h3{
        margin: 2rem 0 0 0;
    }
    @include tablet {
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 3rem;
    
        h3{
            margin: 0;
            text-align: left;
            font-weight: $bold;
        }
    }

}