@include tablet {
    .contenedor-anuncios{
        display: flex;
        justify-content: space-between;
        width: 100%;
    }
}
 
.anuncio{
    background-color: lighten($gris, 8);
    border: 1px solid $gris;
    box-shadow: 5px 2px 2px $negro;
    max-width: 30%;
    word-wrap: break-word; //evitar que la letra se salga del div
    .contenido-anuncio{
        padding: 2rem;
        
    }

    h3, p{
        margin: 0;
    }

    .precio{
        color: $verde;
        font-weight: $bold;
    }

    li{
        list-style: none;
    }


}

.iconos-caracteristicas{
    display: flex;
    li{
        flex: 1;
        display: flex;
            img{
                flex: 0 0 3rem;
                margin-right: 1rem;
            }

            p{
                font-size: 2.3rem;
                font-weight: $black;
            }
    }

}

.boton-derecha{
    display: flex;
    justify-content: flex-end;
}

.contenedor-propiedades{
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;
    justify-content: space-between;
    padding: 0 5rem;
}

.contenedor-solo{
    display: flex;
    justify-content: center;
    padding: 5rem 0;
}

.anuncio-solo{
    width: 50rem;
    background-color: lighten($gris, 8);
    border: 1px solid $gris;
    box-shadow: 5px 2px 2px $negro;
    word-wrap: break-word;
    padding: 0 2rem;
}