.iconos-nosotros{
    @include tablet {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 2rem; 
    }
}


.icono{
    text-align: center;
    img{
        height: 10rem;
        width: auto;
        margin: 0 auto;
    }

    h3{
        text-transform: uppercase;
    }
}
