.derecha{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: auto;
    @include tablet {
        flex-direction: row;
        
    }

    

    .dark-mode-boton{
        width: 4rem;
        cursor: pointer;
        margin-top: 2rem;
        margin-bottom: 2rem;
        filter: invert(100%);

        
        @include tablet {
            margin-top: 0;
            margin-bottom: 0;
        }
    }
}

.barra{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 1rem;
    @include tablet {
        justify-content: space-between;
        flex-direction: row;
        align-items: flex-end;
    }
}

.navegacion{
    height: 0;
    visibility: hidden;//ocultamos la navegacion por defecto
    opacity: 0;//le damos la opacidad de cero para mejorar la transicion
    transition: visibility .3s ease-in-out, opacity 0.5s linear, height 0.5s linear;//creamos la transicion que nos pide tres valores: 
    //el primero es la propiedad a la que le vamos a aplicar la transicion, el segundo son los segundos que va a durar la transicion 
    //y el tercero es el tipo de transicion
    @include tablet {
        display: flex;
        opacity: 1;//volvemos a mostrar la nevegacion cuando no existe el menu de hamburguesa 
        visibility: visible;
        display: flex;
        align-items: center;
        margin-left: 4rem;
    }

    a{
        color: $blanco;
        font-size: 2rem;
        display: block;
        text-align: center;

        @include tablet {
            margin-right: 2rem;

            &:last-of-type{
                margin-right: 0;
            }
        }
    }


}

.mostrar{
    visibility: visible;//mostramos la navegacion cuando existe este mostrar
    opacity: 1;
    height: auto;
}

//menu hamburguesa
.mobile-menu{
    margin-top: 2rem;
    width: 5rem;
    @include tablet {
        display: none;
        visibility: hidden;
    }


}