.dark-mode{
    background-color: darken($grisOscuro, 10);
    a{
        color: $blanco;
    }

    p, h1, h2, h3, h4{
        color: $gris;
    }

    .anuncio{
        background-color: $grisOscuro;
        border: 1px solid $grisOscuro;
        box-shadow: 5px 2px 2px $grisOscuro;

        .icono-negro{
            filter: invert(100%);
        }
    }


    .formulario{
        label, legend, p{
            color: $blanco;
        }

        input:not([type="submit"]), textarea, select{
            border: 1px solid $gris;
            background-color: $grisOscuro;
            color: $blanco;
        }
    }
}