.no-webp .imagen-contacto{
    background: url('../img/encuentra.jpg');
}

.webp .imagen-contacto{
    background: url('../img/encuentra.webp');
}

.imagen-contacto{
    background-position: center center;
    background-size: cover;
    padding: 5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    h2, p{
        color: $blanco;
    }

    h2{
        font-weight: $bold;
        font-size: 4rem;
    }

    p{
        font-size: 1.8rem;
    }
}