.testimonio{
    background-color: $verde;
    font-size: 2.4rem;
    padding: 2rem;
    font-weight: 300;
    color: $blanco;
    border-radius: 30px;
    blockquote{
        position: relative;
        padding-left: 5rem;

        &::before{
            content: '';
            background-image: url(../img/comilla.svg);
            background-repeat: no-repeat;
            height: 8rem;
            width: 6rem;
            display: block;
            position: absolute;
            left: -2rem;
            top: -2rem;
        }
    }

    p{
        text-align: right;
        color: $blanco;
    }
}