@include tablet {
    .seccion-inferior{
        display: grid;
        grid-template-columns: 2fr 1fr;
        column-gap: 2rem;
    }

}

.entrada-blog{
    margin-bottom: 2rem;
    @include tablet {
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }

    &:last-of-type{
        margin-bottom: 0;
    }
}

.texto-entrada{
    a{
        color: $negro;
    }   
    h4{
        margin-top: 1rem;
        @include tablet {
            margin: 0;
        }
        line-height: 1.4;
        

        &::after{
            content: '';
            display: block;

            margin-top: .3rem;
            height: .5rem;
            background-color: $verde;
        }

        @include tablet {
            &::after{
                width: 15rem;
            }
        }
    } 

    span{
        color: $amarillo;
    }
}
