.alerta{
    text-align: center;
    
    text-transform: uppercase;
    
    margin-bottom: 1rem;
    &.error{
        background-color: $rojo;   
        border-radius: 5px;
        padding: 0;
        
        p{
            color: $blanco;
        }
    } 

    &.notificacion{
        background-color: $verde;    
        border-radius: 5px;
        padding: 0;
        
        p{
            color: $blanco;
        }
    } 
    
}

table.propiedades{
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;

    thead{
        background-color: $verde;

        th{
            color: $blanco;
            padding: 2rem;
        }
    }

    .imagen-tabla{
        width: 10rem;
    }

    tbody{
        td{
            text-align: center;
        }
        img{
            margin: 0;
            display: inline-block;
        }
    }
}

.imagen-small{
    width: 20rem;
}